import axios from "axios";
import React, { Component } from "react";
import { AiOutlineCaretRight } from "react-icons/ai";
import { BiBuildingHouse } from "react-icons/bi";
import { IoIosClose } from "react-icons/io";
import { MdFilterList, MdLabel, MdModeEdit, MdSave } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  loadEducationCategories,
  PushPopupAlert,
} from "../../../actions";
import { getAllRoom } from "../../../actions/location";
import {
  createSchool,
  getSchools,
  updateSchool,
  deleteSchool,
} from "../../../actions/schools";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import {
  MainItem,
  SideNavigation,
  SubItem,
  SubItemContainer,
} from "../../../components/Navigation/SideNavigation";
import Table from "../../../components/Table/Table";
import { education_category } from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { toTwoDigit } from "../../../shared/dataTest";
import {
  SchoolsInterface,
  RoomLocationInterface,
  DistrictInterface,
} from "../../../shared/interfaces";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/errors";

export const getSchoolById = (
  schools: SchoolsInterface[],
  school_id: string
): SchoolsInterface => {
  return schools.filter((school) => school._id === school_id)[0];
};
interface AppProps {
  auth: Auth;
  education_categories: education_category[] | null;
  allRoom: RoomLocationInterface[];
  schools: SchoolsInterface[];
  getSchools: Function;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  createSchool: Function;
  getAllRoom: Function;
  updateSchool: Function;
  deleteSchool: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppState {
  loading: boolean;
  loadingRequest: boolean;
  districtFilterStatus: boolean;
  edicationCategoryFilterStatus: boolean;
  provinceFilterStatus: boolean;
  thisEdication: education_category | null;
  thisProvince: RoomLocationInterface | null;
  thisDistrict: DistrictInterface | null;
  district: DistrictInterface[];
  schoolList: SchoolsInterface[];
  modalState: "" | "CREATE" | "UPDATE";
  error: { element: string; msg: string } | null;
  schoolName: string;
  thisSchool: SchoolsInterface | null;
  instituteId: string;
  provinceId: string;
  districtId: string;
}

class index extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingRequest: false,
      districtFilterStatus: false,
      edicationCategoryFilterStatus: false,
      provinceFilterStatus: false,
      thisEdication: null,
      thisProvince: null,
      thisDistrict: null,
      district: [],
      schoolList: this.props.schools,
      modalState: "",
      error: null,
      schoolName: "",
      thisSchool: null,
      instituteId: "",
      provinceId: "",
      districtId: "",
    };
  }

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  setLoadingRequest = (state: boolean) => {
    this.setState({ loadingRequest: state });
  };

  setError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ error: data });
  };

  loadDistrict = async () => {
    try {
      const res = await axios.get<DistrictInterface[]>(
        `${API.APPLICATION}/district`
      );
      this.setState({ district: res.data });
    } catch (error) {
      console.error({ code_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
  };

  componentDidMount = () => {
    if (
      this.props.education_categories === null ||
      this.props.education_categories.length === 0
    ) {
      this.props.loadEducationCategories(this.setLoading);
    }
    if (!this.props.allRoom.length) {
      this.props.getAllRoom((status: boolean, msg: string) => {
        if (!status && msg.length > 0) console.log(msg);
      });
    }
    if (!this.props.schools.length && this.state.loading) {
      this.props.getSchools((status: boolean, msg: string) => {
        console.log(status, msg);
        this.setLoading(status);
        if (msg.length > 0) {
          console.error({ loadingCandidatesError: msg });
        }
      });
    }
    if (!this.state.district.length) {
      this.loadDistrict();
    }
    if (this.props.schools.length) {
      this.setState({ schoolList: this.props.schools });
      this.setLoading(false);
    }
  };

  tableData = (): JSX.Element[] => {
    if (!this.state.schoolList.length) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={5}>
            No School Found
          </td>
        </tr>,
      ];
    }

    return this.state.schoolList.map((school, index) => (
      <tr
        key={index + school._id}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="p-3 text-left whitespace-nowrap">{index + 1}</td>
        <td className="p-3 text-left">{school.school_name}</td>
        <td className="py-3 px-5 text-left">
          {this.props.education_categories &&
          this.props.education_categories.filter(
            (edication) =>
              edication.education_institute_id === school.education_category_id
          ).length > 0
            ? this.props.education_categories.filter(
                (edication) =>
                  edication.education_institute_id ===
                  school.education_category_id
              )[0].education_category_title
            : "No Institute"}
        </td>
        <td className="py-3 px-5 text-left">
          {this.state.district &&
          this.state.district.filter(
            (district) => district.district_id === school.district_id
          ).length > 0
            ? this.state.district.filter(
                (district) => district.district_id === school.district_id
              )[0].district_name
            : "No district"}
        </td>
        <td className="px-3">
          <div className="flex items-center gap-2 float-right">
            <button
              className={`bg-blue-600 text-white font-bold py-1 px-2 flex items-center rounded focus:outline-none ${
                this.state.loadingRequest ? "cursor-not-allowed" : null
              }`}
              title="Edit School"
              disabled={this.state.loadingRequest}
              onClick={() => {
                this.setState({
                  modalState: "UPDATE",
                  thisSchool: school,
                  schoolName: school.school_name,
                  instituteId: school.education_category_id,
                  districtId: school.district_id,
                  provinceId: this.props.allRoom.find((item) =>
                    item.districts.find(
                      (d) => d.district_id === school.district_id
                    )
                  )
                    ? this.props.allRoom.find((item) =>
                        item.districts.find(
                          (d) => d.district_id === school.district_id
                        )
                      )!.province_id
                    : "",
                });
              }}
            >
              <MdModeEdit className="w-4 h-4" />
              Edit
            </button>
            <button
              className={`bg-red-700 text-white font-bold py-1 px-2 flex items-center rounded focus:outline-none ${
                this.state.loadingRequest ? "cursor-not-allowed" : null
              }`}
              title="Delete"
              disabled={this.state.loadingRequest}
              onClick={() => this.handleDelete(school._id)}
            >
              <MdModeEdit className="w-4 h-4" />
              Delete
            </button>
          </div>
        </td>
      </tr>
    ));
  };

  handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!this.state.schoolName.length) {
      return this.setError({
        element: "school_name",
        msg: "School name is required",
      });
    }
    if (!this.state.instituteId.length) {
      return this.setError({
        element: "edication_id",
        msg: "Education Institute is required",
      });
    }

    if (!this.state.districtId.length) {
      return this.setError({
        element: "districtId_id",
        msg: "District is required",
      });
    }

    this.setLoadingRequest(true);
    try {
      let res;
      if (this.state.modalState === "CREATE") {
        res = await axios.post<{
          data: {
            status: boolean;
            school_name: string;
            district_id: string;
            education_category_id: string;
            shool_id: string;
          };
        }>(`${API.APPLICATION}/schools`, {
          school_name: this.state.schoolName,
          district_id: this.state.districtId,
          education_category_id: this.state.instituteId,
        });
        if (res.data) {
          console.log({ res });
          this.setState({
            schoolList: [
              ...this.state.schoolList,
              {
                _id: res.data.data.shool_id,
                status: res.data.data.status,
                school_name: res.data.data.school_name,
                district_id: res.data.data.district_id,
                education_category_id: res.data.data.education_category_id,
              },
            ],
          });
          this.props.createSchool({
            _id: res.data.data.shool_id,
            status: res.data.data.status,
            school_name: res.data.data.school_name,
            district_id: res.data.data.district_id,
            education_category_id: res.data.data.education_category_id,
          });
          this.props.PushPopupAlert({
            title: "New School created successfully",
            theme: PopupAlertTheme.success,
          });
          this.setLoadingRequest(false);
          this.setState({
            schoolName: "",
            districtId: "",
            instituteId: "",
            provinceId: "",
          });
        }
      } else if (this.state.thisSchool && this.state.modalState === "UPDATE") {
        res = await axios.patch<{ update: SchoolsInterface }>(
          `${API.APPLICATION}/schools`,
          {
            school_id: this.state.thisSchool._id,
            school_name: this.state.schoolName,
            district_id: this.state.districtId,
            education_category_id: this.state.instituteId,
          }
        );
        if (res.data) {
          console.log({ res });
          this.props.updateSchool({
            _id: this.state.thisSchool._id,
            status: true,
            school_name: this.state.schoolName,
            district_id: this.state.districtId,
            education_category_id: this.state.instituteId,
          });
          this.props.PushPopupAlert({
            title: "School updated successfully",
            theme: PopupAlertTheme.success,
          });
          this.setLoadingRequest(false);
          this.setState({
            schoolName: "",
            districtId: "",
            instituteId: "",
            provinceId: "",
            thisSchool: null,
            modalState: "",
          });
        }
      }
    } catch (error) {
      console.log({ doc_category_error: error });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      this.setLoadingRequest(false);
    }
  };

  handleDelete = async (schoolId: string) => {
    if (window.confirm("Are you sure do you want to delete this room?")) {
      this.setLoadingRequest(true);
      try {
        const res = await axios.delete<SchoolsInterface>(
          `${API.APPLICATION}/schools/${schoolId}`
        );

        if (res.data) {
          console.log({ res });
          this.props.deleteSchool(schoolId);
          this.props.PushPopupAlert({
            title: "School deleted successfully",
            theme: PopupAlertTheme.success,
          });
        }
      } catch (error) {
        console.log({ province_error: error });
        this.props.PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      this.setLoadingRequest(false);
    }
  };

  filterSchools = (
    FILTER_TYPE: "INSTITUTE" | "DISTRICT",
    FILTER_VALUE: string,
    IGNORE_DISTRICT: boolean = false
  ) => {
    var newArray: SchoolsInterface[] = [];
    if (FILTER_TYPE === "INSTITUTE") {
      if (this.state.thisDistrict && !IGNORE_DISTRICT) {
        if (FILTER_VALUE === "ALL") {
          newArray = this.props.schools.filter(
            (edication) =>
              edication.district_id === this.state.thisDistrict!.district_id
          );
        } else {
          newArray = this.props.schools.filter(
            (edication) =>
              edication.education_category_id === FILTER_VALUE &&
              edication.district_id === this.state.thisDistrict!.district_id
          );
        }
      } else {
        if (FILTER_VALUE === "ALL") {
          newArray = this.props.schools;
        } else {
          newArray = this.props.schools.filter(
            (edication) => edication.education_category_id === FILTER_VALUE
          );
        }
      }
    }
    if (FILTER_TYPE === "DISTRICT") {
      if (this.state.thisEdication) {
        if (FILTER_VALUE === "ALL") {
          newArray = this.props.schools.filter(
            (edication) =>
              edication.education_category_id ===
              this.state.thisEdication!.education_institute_id
          );
        } else {
          newArray = this.props.schools.filter(
            (edication) =>
              edication.district_id === FILTER_VALUE &&
              edication.education_category_id ===
                this.state.thisEdication!.education_institute_id
          );
        }
      } else {
        if (FILTER_VALUE === "ALL") {
          newArray = this.props.schools;
        } else {
          newArray = this.props.schools.filter(
            (edication) => edication.district_id === FILTER_VALUE
          );
        }
      }
    }
    this.setState({ schoolList: newArray });
  };

  countSchool = (
    FILTER_TYPE: "DEFAULT" | "EDUCATION" | "DISTRICT" = "DEFAULT",
    FILTER_VALUE: string = "ALL"
  ): string => {
    let counts: number = 0;
    if (FILTER_TYPE === "DEFAULT") {
      counts = this.state.schoolList.length;
    } else if (FILTER_TYPE === "EDUCATION") {
      counts = this.state.schoolList.filter(
        (item) => item.education_category_id === FILTER_VALUE
      ).length;
    } else if (FILTER_TYPE === "DISTRICT") {
      counts = this.state.schoolList.filter(
        (item) => item.district_id === FILTER_VALUE
      ).length;
    }
    return toTwoDigit(counts);
  };

  render() {
    if (this.state.loading === true) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading schools...
        </div>
      );
    }
    return (
      <React.Fragment>
        <SideNavigation>
          {this.state.loading ? (
            <section className="animate-pulse">
              <div className="bg-gray-400 font-bold mb-2 h-7 px-2 rounded-md cursor-wait"></div>
              <div className="bg-gray-400 font-bold mb-2 h-7 px-2 rounded-md cursor-wait"></div>
              <div className="bg-gray-400 font-bold mb-2 h-7 px-2 rounded-md cursor-wait"></div>
            </section>
          ) : (
            <React.Fragment>
              {/* EDUCATION CATEGORY sections */}
              <span className="font-semibold text-lg text-gray-500">
                Filter by Institute
              </span>
              <section
                className={`mt-2 ${
                  this.state.loadingRequest && "cursor-pointer"
                }`}
                onClick={() =>
                  !this.state.loadingRequest &&
                  this.setState({
                    edicationCategoryFilterStatus:
                      !this.state.edicationCategoryFilterStatus,
                  })
                }
              >
                <MainItem
                  title={
                    !this.state.thisEdication
                      ? "Choose Education Institute"
                      : this.state.thisEdication.education_category_title
                  }
                  active={
                    this.state.edicationCategoryFilterStatus ||
                    this.state.thisEdication !== null
                      ? true
                      : false
                  }
                />
              </section>
              {this.state.edicationCategoryFilterStatus &&
                this.props.education_categories &&
                this.props.education_categories.length > 0 && (
                  <SubItemContainer>
                    {this.props.education_categories &&
                      this.props.education_categories.map(
                        (edication, index) => (
                          <section
                            key={edication.education_institute_id + index}
                            onClick={() =>
                              !this.state.loadingRequest &&
                              (this.setState({
                                thisEdication: edication,
                                edicationCategoryFilterStatus: false,
                              }),
                              this.filterSchools(
                                "INSTITUTE",
                                edication.education_institute_id
                              ))
                            }
                          >
                            <SubItem
                              title={edication.education_category_title}
                              icon={MdLabel}
                              active={
                                this.state.thisEdication
                                  ? edication.education_institute_id ===
                                    this.state.thisEdication
                                      .education_institute_id
                                  : false
                              }
                            />
                          </section>
                        )
                      )}
                  </SubItemContainer>
                )}
              {/* end EDUCATION CATEGORY sections */}

              {/* province sections */}
              <section>
                <span className="font-semibold text-lg text-gray-500">
                  Filter by District
                </span>
                <section
                  className="mt-2"
                  onClick={() =>
                    !this.state.loadingRequest &&
                    this.setState({
                      provinceFilterStatus: !this.state.provinceFilterStatus,
                      edicationCategoryFilterStatus: false,
                      districtFilterStatus: false,
                    })
                  }
                >
                  <MainItem
                    title={
                      !this.state.thisProvince
                        ? "Choose Province"
                        : this.state.thisProvince.province_name
                    }
                    active={
                      this.state.provinceFilterStatus ||
                      this.state.thisProvince !== null
                        ? true
                        : false
                    }
                  />
                </section>
              </section>
              {this.state.provinceFilterStatus &&
                this.props.allRoom &&
                this.props.allRoom.length > 0 && (
                  <SubItemContainer>
                    {this.props.allRoom &&
                      this.props.allRoom.map((province, index) => (
                        <section
                          key={province.province_id + index}
                          onClick={() =>
                            !this.state.loadingRequest &&
                            (this.setState({
                              provinceFilterStatus: false,
                              thisProvince: province,
                              thisDistrict: null,
                            }),
                            this.state.thisEdication &&
                              this.filterSchools(
                                "INSTITUTE",
                                this.state.thisEdication.education_institute_id,
                                true
                              ))
                          }
                        >
                          <SubItem
                            title={province.province_name}
                            icon={MdLabel}
                            active={
                              this.state.thisProvince
                                ? province.province_id ===
                                  this.state.thisProvince.province_id
                                : false
                            }
                          />
                        </section>
                      ))}
                  </SubItemContainer>
                )}
              {/* end province sections */}

              {/* DISTRICT sections */}
              {this.state.thisProvince && (
                <section
                  className="mt-2"
                  onClick={() =>
                    !this.state.loadingRequest &&
                    this.setState({
                      edicationCategoryFilterStatus: false,
                      provinceFilterStatus: false,
                      districtFilterStatus: !this.state.districtFilterStatus,
                    })
                  }
                >
                  <MainItem
                    title={
                      !this.state.thisDistrict
                        ? "Choose District"
                        : this.state.thisDistrict.district_name
                    }
                    active={
                      this.state.districtFilterStatus ||
                      this.state.thisDistrict !== null
                        ? true
                        : false
                    }
                  />
                </section>
              )}
              {this.state.districtFilterStatus &&
                this.state.thisProvince &&
                this.state.thisProvince.districts.length > 0 && (
                  <SubItemContainer>
                    {this.state.thisProvince &&
                      this.state.thisProvince.districts.map(
                        (district, index) => (
                          <section
                            key={district.district_id + index}
                            onClick={() =>
                              !this.state.loadingRequest &&
                              (this.setState({
                                thisDistrict: district,
                                districtFilterStatus: false,
                              }),
                              this.filterSchools(
                                "DISTRICT",
                                district.district_id
                              ))
                            }
                          >
                            <SubItem
                              title={district.district_name}
                              icon={MdLabel}
                              active={
                                this.state.thisDistrict
                                  ? district.district_id ===
                                    this.state.thisDistrict.district_id
                                  : false
                              }
                            />
                          </section>
                        )
                      )}
                  </SubItemContainer>
                )}
              {/* end DISTRICT sections */}
            </React.Fragment>
          )}
        </SideNavigation>

        {/* BODY =================================== */}
        <div className="w-full my-5 px-1">
          {/* header title */}
          <div className="flex items-start justify-between">
            <div className="flex flex-row items-center">
              <BiBuildingHouse className="text-blue-500 w-28 h-28" />
              <div className="flex flex-col">
                <h1 className="text-3xl font-semibold">Schools Management</h1>
                <div className="flex flex-row items-center mx-2 md:gap-3 gap-2">
                  <div className="flex flex-col mt-1">
                    <span className="text-gray-600 text-base">
                      All School(s)
                    </span>
                    <span className="text-gray-600 text-2xl font-bold">
                      {this.countSchool()}
                    </span>
                  </div>
                  {this.props.education_categories &&
                    this.props.education_categories.length > 0 &&
                    this.props.education_categories.map((education, index) => (
                      <div
                        className="flex flex-col mt-1"
                        key={index + education.education_institute_id}
                      >
                        <span className="text-gray-600 text-base">
                          {education.education_category_title}
                        </span>
                        <span className="text-gray-600 text-2xl font-bold">
                          {this.countSchool(
                            "EDUCATION",
                            education.education_institute_id
                          )}
                        </span>
                      </div>
                    ))}
                  {/* {userTabs !== TabTypes.Blocked_Users && ( */}
                  {/* <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">
                    Vaccant Position(s)
                  </span>
                  <span className="text-gray-600 text-2xl font-bold">
                    {labelCounts.activeUser}
                    07
                  </span>
                </div> */}
                  {/* )}
                {/* <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">
                    Blocked User(s)
                  </span>
                  <span className="text-gray-600 text-2xl font-bold">
                    12
                  </span>
                </div> */}
                </div>
              </div>
            </div>
            {/* <div className="flex items-center gap-2 mt-2">
            <label className="font-semibold text-gray-600 py-2">
              Academic period
            </label>
            <select
              className="bg-white border border-gray-700 p-1 focus:outline-none focus:bg-gray-100"
              required
            >
              <option value="">2020 - 2021</option>
            </select>
          </div> */}
          </div>
          {/* end header title */}

          <div className="md:w-98 bg-white mx-auto md:p-2">
            <div className="flex items-start justify-between">
              <div>
                {this.state.thisEdication && (
                  <div className="flex">
                    <div className="flex items-center gap-1 mx-1">
                      <MdFilterList className="text-gray-600" />
                      <span className="font-semibold text-gray-500">
                        Institute:
                      </span>
                      <span className="font-semibold text-blue-700">
                        {this.state.thisEdication.education_category_title}
                      </span>
                    </div>
                    <IoIosClose
                      className="cursor-pointer rounded-full hover:bg-red-500 hover:text-white"
                      onClick={() =>
                        !this.state.loading &&
                        (this.setState({ thisEdication: null }),
                        this.filterSchools("INSTITUTE", "ALL"))
                      }
                      title="Clean Education Institute filter"
                    />
                  </div>
                )}
                {this.state.thisProvince && (
                  <div className="flex">
                    <div className="flex items-center gap-1 mx-1">
                      <MdFilterList className="text-gray-600" />
                      <span className="font-semibold text-gray-500">
                        Location:
                      </span>
                      <span
                        className={`font-semibold ${
                          this.state.thisDistrict
                            ? "text-black cursor-pointer"
                            : "text-blue-700"
                        }`}
                      >
                        {this.state.thisProvince.province_name}
                      </span>
                      {this.state.thisDistrict && (
                        <div className="flex items-center">
                          <AiOutlineCaretRight />
                          <span className="text-blue-700 font-semibold">
                            {this.state.thisDistrict.district_name}
                          </span>
                        </div>
                      )}
                    </div>
                    <IoIosClose
                      className="cursor-pointer rounded-full hover:bg-red-500 hover:text-white"
                      onClick={() =>
                        !this.state.loading &&
                        (this.setState({
                          thisDistrict: null,
                          thisProvince: null,
                        }),
                        this.filterSchools("DISTRICT", "ALL"))
                      }
                      title="Clean location filter"
                    />
                  </div>
                )}
              </div>
              <button
                type="button"
                className="btn btn-blue flex my-1"
                onClick={() => this.setState({ modalState: "CREATE" })}
                disabled={this.state.loadingRequest}
              >
                <MdSave className="h-6 w-6" />
                <span className="ml-2">Add School</span>
              </button>
              {/* <DownloadPDF reportTitle="Testing PDF" /> */}
            </div>
            <Table
              columns={[
                { columnTitle: "#", alignText: "left", padding: "p-3" },
                { columnTitle: "School", alignText: "left", padding: "p-3" },
                { columnTitle: "Institute", alignText: "left", padding: "p-3" },
                { columnTitle: "District", alignText: "left", padding: "p-3" },
                { columnTitle: "", alignText: "left", padding: "p-3" },
              ]}
              rows={this.tableData}
              pagination={true}
              search={true}
              thColor="gray"
            />
          </div>
        </div>

        {/* modal control */}
        {this.state.modalState && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={(): void =>
              this.setState({
                modalState: "",
                thisSchool: null,
                schoolName: "",
                instituteId: "",
                districtId: "",
                provinceId: "",
              })
            }
            displayClose={true}
            backDropClose={false}
            title={
              <div className="text-gray-600 font-bold text-3xl md:ml-4 inline-flex items-center gap-2">
                <BiBuildingHouse className="w-11 h-11 text-gray-500 hover:bg-gray-500 hover:text-gray-50 rounded-full p-0 foucs:outline-none" />
                {this.state.modalState === "CREATE" ? "CREATE " : "UPDATE "}{" "}
                SCHOOL
              </div>
            }
            widthSizeClass={ModalSize.medium}
            padding={{ title: true, body: false, footer: false }}
          >
            <div className="mx-8 mb-8">
              <form onSubmit={(e) => this.handleSubmit(e)}>
                <div className="py-4 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                  <div className="flex flex-col">
                    <label>School name</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block border-2 ${
                        this.state.error &&
                        this.state.error.element === "school_name"
                          ? "border-red-700"
                          : "border-gray-500"
                      }`}
                      placeholder="School name"
                      value={this.state.schoolName}
                      onChange={(e) => {
                        this.setError(null);
                        this.setState({ schoolName: e.target.value });
                      }}
                      disabled={this.state.loadingRequest}
                    />
                    {this.state.error &&
                      this.state.error.element === "school_name" && (
                        <span className="text-red-700">
                          {this.state.error.msg}
                        </span>
                      )}
                  </div>

                  {/* INSTITUTES ================= */}
                  <div className="flex flex-col">
                    <label>Institutes</label>
                    {/* block appearance-none w-full bg-grey-lighter border
                  border-grey-lighter text-grey-darker py-3 px-4 pr-8 rounded */}
                    <select
                      className={`text-left input-white w-full block border-2 py-3 px-4 pr-8 ${
                        this.state.error &&
                        this.state.error.element === "edication_id"
                          ? "border-red-700"
                          : "border-gray-500"
                      }`}
                      value={this.state.instituteId}
                      onChange={(e) =>
                        this.setState({
                          instituteId: e.target.value,
                        })
                      }
                      disabled={this.state.loadingRequest}
                    >
                      <option value="" disabled>
                        Choose Institute
                      </option>
                      {this.props.education_categories &&
                        this.props.education_categories.map(
                          (institute, index) => (
                            <option
                              value={institute.education_institute_id}
                              key={index + institute.education_institute_id}
                            >
                              {institute.education_category_title}
                            </option>
                          )
                        )}
                    </select>
                    {this.state.error &&
                      this.state.error.element === "edication_id" && (
                        <span className="text-red-700">
                          {this.state.error.msg}
                        </span>
                      )}
                  </div>

                  {/* PROVINCE ================= */}
                  <div className="flex flex-col">
                    <label>Province</label>
                    <select
                      className={`text-left input-white w-20 inline-block border-2 border-gray-500`}
                      value={this.state.provinceId}
                      onChange={(e) =>
                        this.setState({ provinceId: e.target.value })
                      }
                      disabled={this.state.loadingRequest}
                    >
                      <option value="" disabled>
                        Choose province
                      </option>
                      {this.props.allRoom &&
                        this.props.allRoom.map((province, index) => (
                          <option
                            value={province.province_id}
                            key={index + province.province_id}
                          >
                            {province.province_name}
                          </option>
                        ))}
                    </select>
                  </div>

                  {/* DISTRICT ================= */}
                  {this.state.provinceId && (
                    <div className="flex flex-col">
                      <label>District</label>
                      <select
                        className={`text-left input-white w-20 inline-block border-2 ${
                          this.state.error &&
                          this.state.error.element === "districtId_id"
                            ? "border-red-700"
                            : "border-gray-500"
                        }`}
                        value={this.state.districtId}
                        onChange={(e) =>
                          this.setState({ districtId: e.target.value })
                        }
                        disabled={this.state.loadingRequest}
                      >
                        <option value="" disabled>
                          Choose district
                        </option>
                        {this.props.allRoom &&
                        this.props.allRoom.filter(
                          (province) =>
                            province.province_id === this.state.provinceId
                        )[0].districts.length > 0 ? (
                          this.props.allRoom
                            .filter(
                              (province) =>
                                province.province_id === this.state.provinceId
                            )[0]
                            .districts.map((district, index) => (
                              <option
                                value={district.district_id}
                                key={index + district.district_id}
                              >
                                {district.district_name}
                              </option>
                            ))
                        ) : (
                          <option key={702} disabled={true}>
                            No District Found
                          </option>
                        )}
                      </select>
                      {this.state.error &&
                        this.state.error.element === "districtId_id" && (
                          <span className="text-red-700">
                            {this.state.error.msg}
                          </span>
                        )}
                    </div>
                  )}
                </div>
                <div className="pt-4 flex justify-between">
                  <button
                    onClick={() =>
                      this.setState({
                        modalState: "",
                        thisSchool: null,
                        schoolName: "",
                        instituteId: "",
                        districtId: "",
                        provinceId: "",
                      })
                    }
                    className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none`}
                    type="button"
                    disabled={this.state.loadingRequest}
                  >
                    Exit
                  </button>
                  <button
                    className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                      this.state.loadingRequest ? "animate-pulse" : null
                    }`}
                    type="submit"
                    disabled={this.state.loadingRequest}
                  >
                    {this.state.loadingRequest ? "Loading..." : "Save"}
                  </button>
                </div>
              </form>
            </div>
          </Modal>
        )}
      </React.Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  schools,
  schoolLevels,
  locations,
}: StoreState): {
  auth: Auth;
  schools: SchoolsInterface[];
  education_categories: education_category[] | null;
  allRoom: RoomLocationInterface[];
} => {
  return {
    auth: auth,
    schools: schools.schools,
    education_categories: schoolLevels.education_categories,
    allRoom: locations.roomLocation,
  };
};

export const Schools = connect(mapStateToProps, {
  createSchool,
  getSchools,
  updateSchool,
  getAllRoom,
  deleteSchool,
  loadEducationCategories,
  PushPopupAlert,
})(index);
